<template lang="en">
    <div>
        <div class="my-popups modal right fade show" style="display:block;">
            <div class="modal d-block ">
                <div class="modal-dialog doc-setup-slide mw-1100" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title"> Configuration</h5>
                            <button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close">
                                <i class="fa fa-times" aria-hidden="true"></i>
                            </button>
                        </div>
                        <div class="modal-body bg-white newtabsdes_configuration_mainbase newtabdes_buttonsbase">
                            <section class="container-fluid p-0" v-if="modueKeys.length > 0">
                                <div class="left-menu-tab new_tab">
                                    <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);">
                                        <ul class="nav nav-pills flex-column left-tab responivecustomtab newtabsdes_configuration">
                                            <li class="nav-item FirstScreen" v-for="item in modueKeys">
                                                <a :class="{'nav-link': true, 'active': item.moduleName.toLowerCase() == currentSelectedModule.moduleName.toLowerCase()}"  style="cursor: pointer;" v-on:click="GetModuleConfigurationKeys(item)">
                                                    <i :class="'pr-2 text-muted ' + getModuleIcon(item.moduleName)"></i>
                                                    {{ $t(item.moduleName) }}
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                <div class="right-content">
                                    <div class="tab-content">
                                        <div id="step1" class="tab-pane call-detail fade p-0 border-0 active show ">
                                            <dynamic-form :schema="formSchema" :buttons="buttons" v-on:OnSubmit="SaveConfigurations">
                                                <template v-slot:tgslot-TicketStartingNumber="{ data }">
                                                    <div class="input-group">
                                                        <input type="text"  class="form-control" v-model="data.fieldInfo.value"/>
                                                        <div class="input-group-append">
                                                            <span class="input-group-text">
                                                                <a id="QuotePreviewtemplate" href="javascript:;" data-key="TicketStartingNumber" v-on:click="CloseOpenTicketPopup(data.fieldInfo,'Ticket Number')">
                                                                    <i class="fa fa-pencil text-info font-18"></i>
                                                                </a>
                                                            </span>
                                                        </div>
                                                        <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block"></span>
                                                    </div>
                                                    <span class="invalid-feedback">{{ data.error }}</span>
                                                </template>
                                                <template v-slot:tgslot-CRMQuotationStartNumber="{ data }">
                                                    <div class="input-group">
                                                        <input type="text"  class="form-control" v-model="data.fieldInfo.value"/>
                                                        <div class="input-group-append">
                                                            <span class="input-group-text">
                                                                <a id="QuotePreviewtemplate" href="javascript:;" data-key="TicketStartingNumber" v-on:click="CloseOpenTicketPopup(data.fieldInfo,'Qoute Number')">
                                                                    <i class="fa fa-pencil text-info font-18"></i>
                                                                </a>
                                                            </span>
                                                        </div>
                                                        <span name="LocationTracking" id="LocationTracking" class="invalid-feedback d-block"></span>
                                                    </div>
                                                    <span class="invalid-feedback">{{ data.error }}</span>
                                                </template>
                                                <template v-slot:tgslot-SmtpPassword="{ data }">
                                                    <div class="input-group">
                                                        <input type="password"  class="form-control" v-model="data.fieldInfo.value"/>                                                        
                                                    </div>                                                  
                                                </template>
                                            </dynamic-form>
                                        </div>
                                    </div>
                                </div>  
                            </section>
                            <section class="container-fluid p-0" v-if="modueKeys.length == 0 && noRecordfound == true">
                                <div class="text-danger text-center p-4">
                                    {{$t("NoRecordfound")}}
                                </div>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <modalConfigureTicketNumberVue v-if="showTicketNumber" :callbackfunction="GetModuleConfigurationKeys" :Message="Message" :coreConfigId = "ticketCoreConfigId" @close="CloseOpenTicketPopup"></modalConfigureTicketNumberVue>
    </div>
</template>
<script>
    import modalConfigureTicketNumberVue from './moduleComponents/ticketing/modalConfigureTicketNumber.vue';
    import dateService from "@/services/DataService";
    export default {
        components: {
            modalConfigureTicketNumberVue
        },
        props: {
            moduleCode: null
        },
        data: function(){
            return {
                modueKeys: [],
                currentSelectedModule: "",
                formSchema: [],
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-success",
                        text: "<i class='fa fa-save pr-2'></i> Save",
                    },
                ],
                showTicketNumber:false,
                ticketCoreConfigId: 0,
                noRecordfound: false,
                Message:''
            }
        },
        created: function() {            
            this.GetConfigurationModule();
        },
        methods: {
            CloseOpenTicketPopup: function(itm,msg) {                
                if(itm) {
                    this.ticketCoreConfigId = itm.customFieldId;
                    this.Message=msg;
                }
                this.showTicketNumber = !this.showTicketNumber;
            },
            GetConfigurationModule: function() {
                var ths = this;
                var params = 'moduleName=' + this.moduleCode;
                dateService.GetModuleForConfiguration(params).then(function (response) {
                    if(response != null && response.data != null && response.data.length > 0){
                        ths.modueKeys = response.data;
                        ths.currentSelectedModule = ths.modueKeys[0];
                        ths.GetModuleConfigurationKeys();
                    } else{
                        ths.noRecordfound = true;
                    }
                })
            },
            GetModuleConfigurationKeys: function(item) {
                var ths = this;
                if(typeof(item) != 'undefined'){
                    ths.currentSelectedModule = item
                }
                var params = 'moduleId=' + ths.currentSelectedModule.moduleId;
                dateService.GetModuleKeys(params).then(function (response) {
                    if(response != null && response.data != null){
                        ths.ConvertConfigurationDataIntoDynamicFormobj(response.data.StorageKeys);
                    }
                });
            },
            Getoption: function (params) {
                params = params == null ? '' : params;
                var options = [];
                $.each(params.split('^'), function (i, v) {
                    options.push(v);
                });
                return options;
            },
            ConvertConfigurationDataIntoDynamicFormobj: function(configurations){
                var ths = this;
                ths.formSchema = [];
                configurations.sort((a,b) => (ths.$t(a.CORE_CONFIG_KEY) > ths.$t(b.CORE_CONFIG_KEY)) ? 1 : ((ths.$t(b.CORE_CONFIG_KEY) > ths.$t(a.CORE_CONFIG_KEY)) ? -1 : 0))
                if(configurations != null && configurations.length > 0){
                    let obj = {
                        layoutType: 'double',
                        groupId: 0,
                        group_name: "",
                        Data: []
                    }
                    configurations.forEach(function(item, index){
                        let validationRules = {}
                        validationRules["required"] = true;
                        let astype = item.ASTYPE
                        let options = []
                        var val = item.CORE_CONFIG_VALUE
                        if(item.CORE_CONFIG_OPTIONS != null){
                            options = ths.MakeArray(item.CORE_CONFIG_OPTIONS, 'radio', '^');
                        }
                        if(item.SELECT_OPTIONS != null && item.SELECT_OPTIONS != ""){
                            options = ths.MakeNormalArray(item.SELECT_OPTIONS);
                        }
                        let comfigMode = "date";
                        let placeholder = "Enter " + ths.$t(item.CORE_CONFIG_KEY);
                        if(astype == "SelectField" || astype == "CustomSelectField" || astype == "TreeSelectField"){
                            placeholder = "Select " + ths.$t(item.CORE_CONFIG_KEY);
                        } else if(astype == "MultiSelectField"){
                            val = [];
                            let valArray = item.CORE_CONFIG_VALUE.split('^')
                            valArray.forEach(function(x){
                                val.push({
                                    label: x,
                                    value: x
                                })
                            });

                        } else if(astype == "CheckBoxField"){
                            val = item.CORE_CONFIG_VALUE.split('^')
                        }
                        if(astype == "DateField"){
                            comfigMode = "date";
                        } else if (astype == "MultiSelectField"){
                            debugger;
                            comfigMode = 'tag'
                        }
                        let fieldObj = {
                            customFieldId: item.CORE_CONFIG_ID,
                            astype: astype,
                            label: ths.$t(item.CORE_CONFIG_KEY),
                            name: item.CORE_CONFIG_KEY,
                            value: val??"",
                            placeholder: placeholder,
                            mode: comfigMode,
                            config: {
                                mode: comfigMode,
                                options: options,
                                dependentOn: null,
                                showWhen: null,
                                valueMapping: null,
                                fieldCode: null
                            },
                            visibility: true,
                            validationRules: validationRules,
                            dtCode: item.CORE_CONFIG_TYPE
                        }
                        obj.Data.push(fieldObj);
                    })
                    ths.formSchema.push(obj);
                }
            },
            SaveConfigurations: function(e, x){
                var ths = this;
                var configObj = []
                ths.formSchema.forEach(function(group, index){
                    group.Data.forEach(function(item, index){
                        var value = item.value;
                        if(item.astype == "CheckBoxField"){
                            value = (item.value.filter((x) => x != "")).join("^");
                        }
                        if(item.astype == 'MultiSelectField'){
                            let val = "";
                            if(typeof(item.value) == 'object'){
                                try{
                                    if(typeof(item.value.length) != 'undefined' && item.value.length > 0 ){
                                        item.value.forEach(function(item){
                                            if(val == ""){
                                                val = item.value;
                                            } else {
                                                val = val + '^' + item.value;
                                            }
                                        })
                                    } else {
                                        val = item.value.value
                                    }
                                }catch(e){
                                    val = item.value.value
                                }
                            } else {
                                val = item.value
                            }
                            value = val;
                        }
                        var obj = {
                            DataId: item.customFieldId,
                            key: item.name,
                            DataValue: value
                        }
                        configObj.push(obj)
                    });
                });
                var paramObj = {
                    configData: configObj,
                    conditionData: {
                        refCode: 'CompanySetting',
                        moduleName: ths.moduleCode
                    }
                }
                var data = JSON.stringify(paramObj);
                dateService.ManageDocUserStorageSettingData(data).then(function (response) {
                    if(response != null && response.data != null && response.data == 1){
                        ths.ShowAlert(ths.$t('RecordsSavedSuccessfully'), 'success');
                    } else {
                        ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t("Alert"));
                    }
                });
            }
        },
    }
</script>
<style>
    .custom-control-label{
        text-transform: capitalize;
    }
</style>