<template>
    <div>
        <component :is="component" :moduleCode="moduleCode" v-on:nextTab="NextTab" v-on:updateProgress="UpdateProgress" v-on:previousTab="PreviousTab" v-on:close="Close" :tabCount="tabCount" :currentTabIndex="currentTabIndex"></component>
    </div>
</template>
<script>
import priority from './ticketing/priority.vue';
import Lead from './CRM/Lead.vue';
import ConfigurationTab from './CRM/ConfigurationTab.vue';
import Location from './asset/Location.vue';
import AddDeployProduct from './asset/AddDeployProduct.vue'
import ProductType from './asset/ProductType.vue'
import AddApprovalChain from './timer/AddApprovalChain.vue';
import AddApprovalGroup from './timer/AddApprovalGroup.vue';
import AddApprovalRule from './timer/AddApprovalRule.vue';
import ChooseStorage from './DocStorage/ChooseStorage.vue'
import StorageManagement from './DocStorage/StorageManagement.vue'
import ClientStorageListing from './DocStorage/ClientStorageListingTable.vue'
import UserStorageListing from './DocStorage/UserStorageListingTable.vue'
import AddGroup from './ticketing/AddGroup.vue';
import AddServiceBoard from './ticketing/AddServiceBoard.vue';

export default {
    components: {
        priority,
        Lead,
        ConfigurationTab,
        Location,
        AddDeployProduct,
        ProductType,
        AddApprovalChain,
        AddApprovalGroup,
        AddApprovalRule,
        ChooseStorage,
        StorageManagement,
        ClientStorageListing,
        UserStorageListing,
        AddGroup,
        AddServiceBoard
    },
    props: {
        moduleCode: "",
        component: "",
        tabCount: 0,
        currentTabIndex: 0
    },
    methods: {
        NextTab: function () {
            this.$emit("NextTab");
        },
        UpdateProgress: function(index, completeStatus) {
            this.$emit("UpdateProgress", index, completeStatus);
        },
        PreviousTab: function(){
            this.$emit("PreviousTab");
        },
        Close: function(){
            this.$emit("Close");
        }
    },
}
</script>